import {
  ChangeDetectorRef,
  Component,
  OnInit
} from '@angular/core';
import {
  MenuController,
  AlertController,
  Platform
} from '@ionic/angular';
import {
  Location
} from "@angular/common";
import {
  Router
} from '@angular/router';
import {
  StatusBar
} from "@ionic-native/status-bar/ngx";
import {
  AngularFireAuth
} from '@angular/fire/compat/auth';
import {
  AngularFirestore
} from '@angular/fire/compat/firestore';
import {
  UserService
} from './__helper/user.service';
import {
  Geolocation
} from "@ionic-native/geolocation/ngx";
import {
  Plugins
} from '@capacitor/core';
const {
  PushNotifications,
  Modals,
  App
} = Plugins;
import {
  PushNotification,
  PushNotificationActionPerformed
} from "@capacitor/push-notifications";
import {
  GoogleAuth
} from '@codetrix-studio/capacitor-google-auth';
import { SplashScreen } from '@capacitor/splash-screen';



@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent implements OnInit {

  name = 'User';
  photoURL = '';
  lastTimeBackPress: number = 0;
  timePeriodToExit: number = 2000;
  backdropVisible = false;

  constructor(public changeDetector: ChangeDetectorRef, private menu: MenuController, private platform: Platform, private alertController: AlertController, private location: Location, private router: Router, private statusBar: StatusBar, private angularAuth: AngularFireAuth, private firestore: AngularFirestore, public userS: UserService, private geolocation: Geolocation) {
    this.initializeApp();

  }

  toggleBackdrop(isVisible) {
    this.backdropVisible = isVisible;
    this.changeDetector.detectChanges();
  }

  navigate(name) {
    this.router.navigate(['/' + name]);
  }

  ngOnInit() {
    this.geolocation.getCurrentPosition();
  }

  notification() {

    if (this.platform.is("android") || this.platform.is("ios")) {
      // Some issue with our setup and push will not work
      PushNotifications.addListener('registrationError',
        (error: any) => {
          // alert('Error on registration: ' + JSON.stringify(error));
          console.log('Error on registration: ' + JSON.stringify(error));
        }
      );

      PushNotifications.addListener('pushNotificationReceived',
        (notification: PushNotification) => {
          var audio1 = new Audio('assets/audio.mp3');
          console.log('Audio');
          audio1.play();
          // alert('Push received: ' + JSON.stringify(notification));
          console.log('Push received: ', notification);

          let alertRet = Modals.alert({
            title: notification.title,
            message: notification.body
          });

        }
      );

      // Method called when tapping on a notification
      PushNotifications.addListener('pushNotificationActionPerformed',
        (notification: PushNotificationActionPerformed) => {

          console.log('Push action performed: ' + notification);
        }
      );
    }

  }

  logout() {
    this.angularAuth.signOut().then(res => {
      this.router.navigate(['/auth/signin']);
      this.userS.userAge = '';
      this.userS.userCountry = '';
      this.userS.userDob = '';
      this.userS.userDocId = '';
      this.userS.userEmail = '';
      this.userS.userGender = '';
      this.userS.userName = '';
      this.userS.userPhoneNumber = '';
      this.userS.userPhotoUrl = '';
      this.userS.userProvider = '';
      this.userS.userReferalCode = '';
      this.userS.userUID = '';
    });
  }

  initializeApp() {
    this.platform.ready().then(async () => {
      console.log("Platform ready");
      // await SplashScreen.hide();
      GoogleAuth.init();
      this.statusBar.backgroundColorByHexString('#FF6000');
      this.statusBar.styleLightContent();
    });

  }

  async presentAlertConfirm() {
    const alert = await this.alertController.create({
      header: 'Confirm !',
      message: 'Confirm to Exit App !',
      mode: 'ios',
      buttons: [{
        text: 'Cancel',
        role: 'cancel',
        cssClass: 'secondary',
        handler: (blah) => {}
      }, {
        text: 'Exit',
        handler: () => {
          navigator["app"].exitApp();
        }
      }]
    });
    await alert.present();
  }

  closeFirst() {
    this.menu.close('first');
  }
}
